/* eslint-disable max-len */
let css = (
    '.sparkle-effect {' +
        'display: block;' +
        'position: absolute;' +
        'top: 50%;' +
        'left: 50%;' +
        'background-repeat: no-repeat;' +
        'background-position:center;' +
        'background-size: 100% 100%;' +
        'background-image: url(data:image/png;base64,);' +
        'overflow: hidden;' +
        'z-index: 2;' +
        'color: tansparent;' +
        'opacity: 0.0;' +
        'animation: glitter 6s linear 0s infinite normal;' +
    '}' +

    '.sparkle-effect.small {' +
    'width: 20px;' +
    'height: 20px;' +
    '}' +

    '.sparkle-effect.medium {' +
    'width: 30px;' +
    'height: 30px;' +
    '}' +

    '.sparkle-effect.large {' +
    'width: 50px;' +
    'height: 50px;' +
    '}' +

    '@keyframes glitter {' +
    '0% {' +
        'transform: scale(0.3) rotate(0deg);' +
        'opacity: 0;' +
    '}' +
    '25% {' +
        'transform: scale(1) rotate(360deg);' +
        'opacity: 1;' +
    '}' +
    '50% {' +
        'transform: scale(0.3) rotate(720deg);' +
        'opacity: 0;' +
    '}' +
    '100% {' +
        'transform: scale(0.3) rotate(0deg);' +
        'opacity: 0;' +
    '}' +
    '}'
);
/* eslint-enable */

class Sparkle {
    constructor(node, opts) {
        let style = document.createElement('style');
        style.type = 'text/css';
        if (style.styleSheet){
            style.styleSheet.cssText = css;
        } else {
            style.appendChild(document.createTextNode(css));
        }

        this.node = node;
        this.node.appendChild(style);

        this.opts = _.defaults(opts, {
            stars: 100,
            sparkle: 20,
        });

        this.createStars();
    }

    createStars() {
        let size = 'small';

        for (let i = 0; i < this.opts.stars; i++) {
            if (i % 2 === 0) {
                size = 'small';
            } else if (i % 3 === 0) {
                size = 'medium';
            } else {
                size = 'large';
            }

            this.createStar(size);
        }
    }

    createStar(size) {
        let sparkle = document.createElement('DIV');
        sparkle.className = `sparkle-effect ${size}`;
        sparkle.setAttribute('style',
            `top: ${(Math.random() * 100) + '%'}; left: ${(Math.random() * 100) + '%'};`
        );

        this.node.appendChild(sparkle);
    }
}

export default function (node, opts) {
    return new Sparkle(node, opts);
}



